html,
body {
  padding: 0;
  margin: 0;
  max-width: 100%;
  height: auto !important;
}
.swiper-button-prev,
.swiper-button-next {
  margin-top: -100px !important;
}

@-ms-viewport {
  width: device-width !important;
}

.swiper-container {
  width: 700px;
  height: 600px;
  margin-top: 7em;
  text-align: center;
  overflow: hidden;
}
.swiper-slide {
  text-align: center;
}

.swiper-pagination {
  bottom: 3em !important;
}

#intro-child {
  display: flex;
  justify-content: center;
}
a {
  color: inherit;
  text-decoration: none;
}
html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}
* {
  margin: 0;
  padding: 0;
}
.burger-btn {
  width: 28px;
  height: 3px;
  background-color: white;
  margin: 6px 0;
}
#burger-parent {
  position: absolute;
  justify-content: center;
  cursor: pointer;
  bottom: 16px;
  left: 90%;
  display: none;
}

#navbar {
  background-color: #3f3c3c;
  position: sticky;
  margin-top: -10px;
  padding: 12px;
  border-bottom: 3px solid #8edbee;
  top: 0;
  z-index: 999;
  font-family: "Montserrat Black";
  text-transform: uppercase;
  font-size: 17px;
  display: block;
  transition: 1s all ease-out;
}

#navbar li {
  display: inline;
  list-style-type: none;
  color: white;
  transition: all 0.4s ease;
  cursor: pointer;
  margin: 15px;
}
#navbar li:hover {
  color: rgb(228, 88, 88);
}
.check-work-btn {
  border: 2px solid #fff;
  text-decoration: none;
  color: #fff;
  padding: 20px;
  font-size: 1.3em;
  margin-top: 1em;
  font-family: "Montserrat Black";
  text-transform: uppercase;
  position: relative;
  height: 15px;
  line-height: 15px;
  transition: all 0.2s ease-in-out;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1, 1);
}

.check-work-btn:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background-color: rgb(34, 33, 33);
  transition: 0.3s;
  z-index: -3;
}
.check-work-btn:hover:before {
  width: 100%;
}
.check-work-btn:hover {
  transition: all 0.3s;
}

#check-work-parent {
  display: flex;
  justify-content: center;
}
canvas {
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 13s ease infinite;
  height: calc(100vh + 32px);
  width: 100vw;
}
h3 {
  text-align: center;
  margin: 1em;
}

#technology {
  position: absolute;
}

.button {
  display: inline-block;
  padding: 0.75rem 1.25rem;
  border-radius: 99999px;
  color: #fff;
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 0.15rem;
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
  margin: 12px;
  z-index: 1;
  text-decoration: none;
  width: 60px;
}
.button:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #0cf;
  z-index: -2;
}
.button:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background-color: #099dc2;
  transition: all 0.3s;
  border-radius: 10rem;
  z-index: -1;
}

.button:hover {
  color: #fff;
}
.button:hover:before {
  width: 100%;
}
#buttons {
  display: flex;
  flex-direction: row;
}
#parent {
  z-index: 2;
  position: absolute;
  text-align: center;
  width: 100%;
  top: 22em;
  text-align: justify;
}

#Projects {
  background-color: #f5f5f5;
  padding: 1em;
}

.tech-used {
  font-style: italic;
  font-size: 16px;
  font-weight: 400;
  color: rgb(37, 32, 32);
}

.tech {
  color: rgb(0, 0, 0);
  font-weight: bold;
}

#project-parent {
  display: flex;
  justify-content: center;
}

.project-name {
  text-align: center;
  font-family: "Open Sans", sans-serif;
  font-size: 30px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #252627;
  padding-bottom: 1em;
}

p {
  text-align: center;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 20px;
  color: #252627;
  padding: 20px;
  font-weight: 500;
}

.project-img {
  border-radius: 10px;
  width: 500px;
  object-fit: cover;
}

#contact-form {
  display: flex;
  justify-content: center;
  margin-top: 1em;
  flex-direction: column;
  text-align: center;
}

.col-form {
  padding: 10px;
  width: 400px;
  position: relative;
  top: 40px;
  margin: 10px;
  border: 0;
  outline: 0;
  background: transparent;
  border-bottom: 1px solid rgb(121, 116, 116);
  font-family: "Montserrat", sans-serif;
}

.col-message {
  padding: 10px;
  width: 400px;
  position: relative;
  top: 40px;
  margin: 10px;
  height: 200px;
  resize: none;
  background: transparent;
  border-bottom: 1px solid rgb(121, 116, 116);
  font-family: "Montserrat", sans-serif;
}

.col-submit {
  width: 420px;
  padding: 10px;
  margin: 40px;
  background-color: transparent;
  border: 1px rgb(71, 59, 59) solid;
  outline: none;
  transition: 0.3s ease all;
  cursor: pointer;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  position: relative;
}
.col-submit:hover {
  background-color: black;
  color: white;
}

#contact-title {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 50px;
  text-transform: uppercase;
  letter-spacing: 4px;
  color: #444649;
}
#footer {
  background-color: #888080;
  border-top: 2px #3dadd3 solid;
  height: 70px;
}
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: rgb(83, 76, 76);
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.fa.fa-github {
  position: relative;
  font-size: 40px;
  margin-left: 60px;
}
.fa.fa-codepen {
  position: relative;
  text-align: center;
  font-size: 40px;
  margin-left: 60px;
}
.trademark {
  font-size: 13px;
  color: black;
  text-align: center;
  position: relative;
  bottom: 15px;
  font-family: "Montserrat", sans-serif;
  letter-spacing: -0.4px;
}

@media only screen and (max-width: 700px) {
  .project-img {
    width: 80vw;
  }
  .trademark {
    text-align: right;
    right: 7em;
  }
  #logos {
    position: relative;
    bottom: 1em;
  }
  .fa.fa-github {
    margin: 0;
    font-size: 30px;
    top: 29px;
    left: 0.2em;
  }
  .fa.fa-codepen {
    right: 136px;
    top: 29px;
    right: 2.5em;
    font-size: 30px;
    margin-left: 100px;
  }
}
@media only screen and (max-width: 400px) {
  .button {
    margin: 10px;
  }
  .col-submit {
    right: 1.5em;
  }
}

#name {
  color: white;
}

#skill {
  height: 100px;
  transition: all 0.2s ease-in-out;
  margin: 40px;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1, 1);
}

#skill:hover {
  transform: scale(1.1);
}

#skills {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: -0.4em;
}

#skills-title {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 50px;
  text-transform: uppercase;
  letter-spacing: 4px;
  color: #444649;
}
#project-title {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 50px;
  position: absolute;
  text-transform: uppercase;
  letter-spacing: 4px;
  color: #444649;
}

h1 {
  text-align: center;
  font-family: "Montserrat Black";
  font-size: 32pt;
  text-align: justify;
  text-shadow: -5px 1px 2px rgb(197, 54, 54);
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@media only screen and (max-width: 610px) {
  h1 {
    font-size: 40px;
    text-align: center;
  }
  #burger-parent {
    display: block;
  }

  #navbar-child {
    display: none;
    background-color: #4b5a6d;
    width: 100vw;
    padding-right: 0s;
    height: 12em;
    position: relative;
    right: 2em;
    margin-top: 2.2em;
    transition: 1s;
  }

  #navbar {
    padding: 2em;
    height: 0;
    transition: 1s;
  }
  #navbar li {
    display: block;
    position: relative;
    top: 1em;
    margin: 25px;
  }
}
@media only screen and (max-width: 815px) {
  #skill {
    margin: 10px;
    height: 55px;
    margin-top: 2.4em;
  }

  .project-description {
    font-size: 15px;
  }

  .swiper-button-prev,
  .swiper-button-next {
    display: none !important;
  }

  #parent {
    margin-top: -8em;
  }
  .col-form {
    width: 300px;
  }
  .col-submit {
    width: 320px;
    text-align: center;
    justify-content: right;
  }
  .col-message {
    width: 300px;
  }
}
@media only screen and (min-width: 611px) {
  #navbar-child {
    display: block !important;
  }
}
@media only screen and (max-width: 375px) {
  .col-submit {
    margin-left: 3.6em;
  }
}
@media only screen and (max-width: 360px) {
  .col-submit {
    margin-left: 3em;
  }
}
